<template>
  <div>
  <v-card flat style="border-radius: 4px" id="headFactura">
    <v-toolbar
      color="primary"
      dark
      dense
      >
      <v-btn color="primary" class="mr-2" fab small @click="$router.push('/pqrs')">
        <v-icon>arrow_back</v-icon>
      </v-btn>
      <v-toolbar-title>Informacin</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>
    <v-card-text style="padding: 5px 16px 5px 16px">
      <v-row align-center>
        <v-col>
          <v-flex>
            <div class="title">
              {{`# Reclamo: ${this.valBarcode}`}}
            </div>
            <div class="subtitle"><b>Fecha:</b> {{this.txtfecha}}</div>
            <v-layout align-center>
              <div class="subtitle"><b>Remitente:</b> {{this.txtnombre}}</div>
            </v-layout>
            <div class="subtitle"><b>Direccin:</b> {{this.txtdireccion}}</div>
            <div class="subtitle"><b>Telfono:</b> {{this.txttelefono}}<span><v-btn icon :href="`https://api.whatsapp.com/send?phone=57${this.txttelefono}`" target="_blank"><v-icon>phone</v-icon></v-btn></span></div>
            <div class="subtitle"><b>Email:</b> {{this.txtemail}}</div>
          </v-flex>
        </v-col>
        <v-col>
          <v-flex align-self-end>
            <div class="subtitle"><b>Tipo de comunicacin:</b> {{this.txttipocomunica}}</div>
            <div class="subtitle"><b>Asunto:</b> {{this.txtasunto}}</div>
            <div class="subtitle"><b>Prioridad:</b> {{this.txtprioridad}}</div>
            <div class="subtitle"><b>Confidencialidad:</b> {{this.txtconfidencialidad}}</div>
            <div class="subtitle"><b>Municipio:</b> {{this.txtmunicipio}}</div>
            <div class="subtitle"><b>Medio solicitante:</b> {{this.txtmediosolicita}}</div>
            <div class="subtitle"><b>Estado:</b> <v-chip :color="estado_color" class="subtitle ml-2" text-color="white">{{this.txtestado}}</v-chip></div>
          </v-flex>
        </v-col>
        <v-col cols="3" style="display: flex;align-items: center;justify-content: center;">
          <v-row>
            <v-col>
              <div>
                <barcode :value="valBarcode" width="1" height="50">
                </barcode>
              </div>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row>
        <v-btn color="primary" outlined small @click="$emit('set-show-hist')" class="mx-1">{{textBtnCommits}}</v-btn>
        <v-btn color="primary" outlined small @click="openRespuesta()" class="mx-1">Respuesta</v-btn>
        <v-btn color="primary" outlined small @click="previewDoc()" class="mx-1">Previsualizar respuesta</v-btn>
        <v-btn :color="validarColor(this.vb_lider)" outlined small @click="changevbresponsable()" class="mx-1">VoBo Responsable</v-btn>
        <v-btn :color="validarColor(this.vb_director)" v-if="requierevb === 1" @click="changevbdireccion()" outlined small class="mx-1">VoBo Directiva</v-btn>
        <v-btn color="primary" outlined small @click="openDocFirmado()" class="mx-1">Documento Firmado</v-btn>
        <v-btn color="green" @click="cerrarRegistro()" outlined small class="mx-1" :disabled="txtestado=='Cerrada'">Cerrar</v-btn>
        <v-spacer></v-spacer>
      </v-row>
    </v-card-text>
  </v-card>
  <!-- Dialog respuesta -->
  <v-dialog v-model="dialog" width="900" persistent>
    <v-card>
      <v-card-title>
        <v-layout class="justify-center align-center">
          Proyectar respuesta
          <v-spacer></v-spacer>
          <v-btn @click="dialog=!dialog" outlined color="primary" small>Cerrar</v-btn>
          <v-btn color="primary" small class="ml-2" @click="saveRespuesta()" :disabled="txtestado=='Cerrada'">Guardar</v-btn>
        </v-layout>
      </v-card-title>
      <v-card-text>
        <!-- <v-textarea
          v-model="respuesta"
          outlined
          label="Cuerpo Respuesta"
          rows="7"
        ></v-textarea> -->
          <vue-editor
            v-model="respuesta"
            id="editor2"
            :editor-toolbar="customToolbar"
            :disabled="txtestado=='Cerrada'"
          >
        </vue-editor>
        <v-file-input
          class="mt-4"
          dense
          outlined
          v-model="txtadjuntos"
          multiple
          small-chips
          color="primary"
          label="Adjuntar Documentos"
        >
        </v-file-input>
        <v-row v-if="items_adjuntos.length > 0">
          <v-col class="mx-4">
            <v-chip
              class="ma-2"
              color="primary"
              close
              v-for="(item, index) in items_adjuntos"
              :key="index"
              cols="auto"
              @click="downloadAdjunto(item.ruta_adjunto)"
              @click:close="deleteAdjunto(item,1)"
            >
              {{ item.nombre }}
            </v-chip>
          </v-col>
        </v-row>
        <v-layout class="align-center justify-center">
          <v-btn @click="dialog_comment=true" outlined color="primary">
            <v-icon class="mx-1" color="primary">chat_bubble_outline</v-icon>
            Aadir comentario
          </v-btn>
        </v-layout>
        <v-container class="pa-0 mt-2" v-if="comentarios.length > 0">
          <v-row>
            <v-col>
            <v-card
              class="mx-auto"
            >
              <v-toolbar
                color="primary"
                dark
                dense
              >
                <v-toolbar-title>Comentarios</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>

              <v-list three-line>
                <template v-for="(item, index) in comentarios">
                  <v-list-item
                    :key="index"
                  >
                    <v-list-item-avatar>
                      <v-avatar color="primary">
                        <span class="white--text headline">{{iniciales}}</span>
                      </v-avatar>
                    </v-list-item-avatar>

                    <v-list-item-content>
                      <v-list-item-title>
                        <v-layout>
                          <div v-html="item.nombre"></div>
                          <v-spacer></v-spacer>
                          <div class="text--secondary caption">{{item.fecha}}</div>
                        </v-layout>
                      </v-list-item-title>
                      <v-list-item-subtitle v-html="item.comentario"></v-list-item-subtitle>
                      <!-- <v-list-item-subtitle>
                        <v-layout class="ma-2">
                          <v-spacer></v-spacer>
                            <template v-for="adj in item.adjuntos">
                              <v-btn small outlined color="primary" :key="adj.id" :href="adj.ruta_adjunto" target="_blank" class="mx-1">{{adj.nombre}}</v-btn>
                            </template>
                            <v-btn @click="dialog_comment=true" icon>
                              <v-badge overlap color="error lighten-3">
                                <template v-slot:badge v-if="item.cantidad>0">{{ item.cantidad }}</template>
                                <v-icon color="primary">chat_bubble_outline</v-icon>
                              </v-badge>
                            </v-btn>
                        </v-layout>
                      </v-list-item-subtitle> -->
                    </v-list-item-content>
                  </v-list-item>
                  <v-divider
                    v-if="index + 1 < comentarios.length"
                    :key="index"
                  ></v-divider>
                </template>
              </v-list>
            </v-card>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
    </v-card>
  </v-dialog>
  <!-- Dialog comentarios -->
  <v-dialog
  v-model="dialog_comment"
  persistent
  max-width="400">
    <v-card
      class="mx-auto"
    >
      <v-card-title class="headline" >
          <v-layout class="align-center justify-center">
          </v-layout>
      </v-card-title>
      <v-card-text>
        <div>
          <v-layout class="headline font-weight-bold align-center justify-center mb-2">
            Aadir comentario
          </v-layout>
          <v-textarea
            outlined
            label="Comentario"
            v-model="txtcomentario"
          ></v-textarea>
          <v-layout class="align-center justify-center">
            <v-btn color="success" @click="saveComentario()"><v-icon class="mr-1">save</v-icon>Guardar</v-btn>
          </v-layout>
        </div>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" text @click="dialog_comment = false">Cerrar</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  <!-- Dialog Upload -->
  <v-dialog
  v-model="dialog_upload"
  persistent
  max-width="400">
    <v-card
      class="mx-auto"
    >
      <v-card-title class="headline" >
        <v-layout class="align-center justify-center">
          Adjuntar documento firmado
        </v-layout>
      </v-card-title>
      <v-card-text>
        <div>
          <v-layout class=" font-weight-bold align-center justify-center mb-2">
            El documento que se adjunte a continuacin deber contener la firma digital, ya que ser el documento final que se enviar como respuesta final.
          </v-layout>
          <v-row v-if="doc_firmado.length > 0">
            <v-col class="mx-4">
              <v-chip
                class="ma-2"
                color="primary"
                close
                v-for="(item, index) in doc_firmado"
                :key="index"
                cols="auto"
                @click="downloadAdjunto(item.ruta_adjunto)"
                @click:close="deleteAdjunto(item,2)"
              >
                {{ item.nombre }}
              </v-chip>
            </v-col>
          </v-row>
          <v-row v-else-if="doc_firmado.length == 0">
            <v-file-input
              class="mt-4"
              dense
              outlined
              v-model="txtdocfirmado"
              small-chips
              color="primary"
              label="Adjuntar Documento"
              @change="saveRespuestaFinal()"
            >
            </v-file-input>
          </v-row>
        </div>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" text @click="dialog_upload = false">Cerrar</v-btn>
        <!-- <v-btn color="green" text @click="saveRespuestaFinal()">Guardar</v-btn> -->
      </v-card-actions>
    </v-card>
  </v-dialog>
  </div>
</template>
<script>
import PqrService from "../../servicios/PqrService"
import { VueEditor } from 'vue2-editor'
import configService from '../../servicios/config'
export default {
  components: { VueEditor },
  data () {
    return {
      nombreresponsable: '',
      cargo: '',
      iniciales: '',
      requierevb: '',
      txtconsecutivo: '',
      txtfecha: '',
      txtnombre: '',
      txtdireccion: '',
      txttelefono: '',
      txtemail: '',
      txttipocomunica: '',
      txtasunto: '',
      txtprioridad: '',
      txtmediosolicita: '',
      txtconfidencialidad: '',
      txtmunicipio: '',
      txtcomentario: '',
      txtdocfirmado: [],
      txtadjuntos: [],
      comentarios: [],
      valBarcode: '',
      usuarionormal: true,
      id_estado: '',
      txtestado: '',
      estado_color: '',
      dialog: false,
      dialog_comment: false,
      dialog_upload: false,
      respuesta: '',
      vb_lider: '',
      vb_director: '',
      customToolbar: [
        [{ header: [false, 1, 2, 3, 4, 5, 6] }],
        ["bold", "italic", "underline", "strike"], // toggled buttons
        [
          { align: "" },
          { align: "center" },
          { align: "right" },
          { align: "justify" }
        ],
        ["blockquote", "code-block"],
        [{ list: "ordered" }, { list: "bullet" }, { list: "check" }],
        [{ indent: "-1" }, { indent: "+1" }], // outdent/indent
        [{ color: [] }, { background: [] }], // dropdown with defaults from theme
        ["image"]
      ],
      doc_firmado: [],
      items_adjuntos: [],
      items_comments: []
    }
  },
  created () {
    var nombre = this.$session.get('nombre').split(" ",2)
    this.iniciales = nombre[0].substring(0,1) + nombre[1].substring(0,1)
  },
  async mounted () {
    this.getData()
    this.getResponsable()
    this.refreshComentariosRespuesta()
    const {data} = await PqrService.getRespuesta(this.$route.params.id)
    if (data.length > 0) {
      this.respuesta = data[0].comentario
    }
    const info = await PqrService.getInfoDocumentoFirmado(this.$route.params.id)
    if (info.data.length > 0) {
      this.doc_firmado = info.data
    }
  },
  methods: {
    async changevbresponsable() {
      if (this.usuarionormal === false) {
        const {data} = await PqrService.changevbresponsable(this.$session.get('usuarioid'),this.$route.params.id)
        if (data.tipo === 'success') {
          this.$toasted.success('Registro actualizado correctamente', { icon: 'done', duration: 3000 })
          this.getData()
        } else {
          this.$toasted.error('Error', { icon: 'done', duration: 3000 })
        }
      }
    },
    async changevbdireccion() {
      if (parseInt(this.$session.get('usuarioid')) === 2) {
        const {data} = await PqrService.changevbdireccion(this.$session.get('usuarioid'),this.$route.params.id)
        if (data.tipo === 'success') {
          this.$toasted.success('Registro actualizado correctamente', { icon: 'done', duration: 3000 })
          this.getData()
        } else {
          this.$toasted.error('Error', { icon: 'error', duration: 3000 })
        }
      }
    },
    async cerrarRegistro() {
      if (parseInt(this.requierevb) === 1) {
        if (parseInt(this.vb_lider) === 1 && parseInt(this.vb_director) === 1) {
          const {data} = await PqrService.cerrarPqr(this.$session.get('usuarioid'),this.$route.params.id)
          if (data.tipo === 'success') {
            this.$toasted.success('Registro actualizado correctamente', { icon: 'done', duration: 3000 })
            this.getData()
            window.open(configService.apiUrl + '/generateStickerPqrsdfSalida?id=' + this.$route.params.id, '_blank')
          } else {
            this.$toasted.error('Error', { icon: 'error', duration: 3000 })
          }
        } else {
          this.$toasted.error('No es posible cerrar un documento sin ambos VoBo.', { icon: 'done', duration: 3000 })
        }
      } else {
        if (parseInt(this.vb_lider) === 1) {
          if (this.doc_firmado.length === 0) {
            this.$toasted.error('No es posible cerrar el registro sin antes tener la respuesta final adjunta.', { icon: 'error', duration: 3000 })
          } else {
            const {data} = await PqrService.cerrarPqr(this.$session.get('usuarioid'),this.$route.params.id)
            if (data.tipo === 'success') {
              this.$toasted.success('Registro actualizado correctamente', { icon: 'done', duration: 3000 })
              this.getData()
              window.open(configService.apiUrl + '/generateStickerPqrsdfSalida?id=' + this.$route.params.id, '_blank')
            } else {
              this.$toasted.error('Error', { icon: 'error', duration: 3000 })
            }
          }
        } else {
          this.$toasted.error('No es posible cerrar un documento sin el VoBo.', { icon: 'done', duration: 3000 })
        }
      }
    },
    downloadAdjunto(url) {
      const win = window.open(url, '_blank')
      win.focus()
    },
    async deleteAdjunto(item,opcion) {
      if (this.txtestado == 'Cerrada') {
        this.$toasted.error('No es posible eliminar el documento firmado despus de cerrado el registro.', { icon: 'error', duration: 3500 })
      } else {
        var nombre = item.ruta_adjunto.substring(57)
        const {data} = await PqrService.deleteFile(nombre, item.id)
        if (data.tipo === 'success') {
          if (opcion == 1) this.openRespuesta()
          else this.openDocFirmado()
        }
      }
    },
    async getResponsable () {
      try {
        const {data} = await PqrService.getResponsablepqr(this.$session.get('usuarioid'),this.$route.params.id)
        this.nombreresponsable = data.nombre
        this.cargo = data.cargo
        this.usuarionormal = data.respuesta
      } catch (error) {
        console.log(error)
      }
    },
    async previewDoc () {
      const {data} = await PqrService.getRespuesta(this.$route.params.id)
      if (data.length > 0) {
        window.open(configService.apiUrl + '/previewpdfPqrsdf?id=' + this.$route.params.id, '_blank')
      } else {
        this.$toasted.error('No se puede previsualizar el documento sin antes tener definido un cuerpo de respuesta', { icon: 'error', duration: 3500 })
      }
    },
    async refreshComentariosRespuesta() {
      try {
        const {data} = await PqrService.getComentariosRespuesta(this.$route.params.id)
        this.comentarios = data
      } catch (error) {
        console.log(error)
      }
    },
    async saveComentario() {
      var info = {
        id_reclamo: this.$route.params.id,
        comentario: this.txtcomentario,
        id_usuario: this.$session.get('usuarioid'),
        respuesta: 1
      }
      try {
        const {data} = await PqrService.saveComentario(info)
        if (data.tipo === 'success') {
          this.$toasted.success('Registro actualizado correctamente', { icon: 'done', duration: 3000 })
        } else {
          this.$toasted.error('Error al guardar archivo oficio', { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.dialog_comment = false
        this.txtcomentario = ''
        this.refreshComentariosRespuesta()
      }
    },
    async saveRespuesta() {
      var info = {
        id_reclamo: this.$route.params.id,
        comentario: this.respuesta,
        id_usuario: this.$session.get('usuarioid')
      }
      try {
        const {data} = await PqrService.saveRespuesta(info,this.txtadjuntos)
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
          this.getData()
        } else {
          this.$toasted.error(data.respuesta, { icon: 'done', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.dialog = false
      }
    },
    async saveRespuestaFinal() {
      var info = {
        id_reclamo: this.$route.params.id,
      }
      const formData = new FormData()
      formData.append('archivos', this.txtdocfirmado)
      formData.append('obj', JSON.stringify(info))
      try {
        const {data} = await PqrService.saveRespuestaFinal(formData)
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
          this.openDocFirmado()
        } else {
          this.$toasted.error(data.respuesta, { icon: 'done', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      }
    },
    async openDocFirmado() {
      const {data} = await PqrService.getInfoDocumentoFirmado(this.$route.params.id)
      this.txtdocfirmado = []
      this.doc_firmado = []
      if (data.length > 0) {
        this.doc_firmado = data
      }
      this.dialog_upload = true
    },
    async openRespuesta() {
      const {data} = await PqrService.getRespuesta(this.$route.params.id)
      if (data.length > 0) {
        this.respuesta = data[0].comentario
        this.items_adjuntos = data[0].adjuntos
      }
      this.dialog = true
      this.txtadjuntos = []
    },
    validarColor(val) {
      if (parseInt(val) === 0) {
        return 'primary'
      } else if (parseInt(val) === 1) {
        return 'green'
      }
    },
    async getData() {
      const {data} = await PqrService.listPqr(this.$route.params.id)
      this.requierevb = data.pqrs_reclamos[0].requierevb_director
      this.valBarcode = data.pqrs_reclamos[0].consecutivo
      this.txtfecha = data.pqrs_reclamos[0].fecha_creacion
      this.txtnombre = data.pqrs_reclamos[0].remitente
      this.txtdireccion = data.pqrs_reclamos[0].direccion
      this.txttelefono = data.pqrs_reclamos[0].telefono
      this.txtemail = data.pqrs_reclamos[0].email
      this.txttipocomunica = data.pqrs_reclamos[0].descripcion
      this.txtasunto = data.pqrs_reclamos[0].asunto
      this.txtprioridad = data.pqrs_reclamos[0].prioridad
      this.id_estado = data.pqrs_reclamos[0].id_estado
      this.txtmediosolicita = data.pqrs_reclamos[0].medio_solicitante
      this.txtmunicipio = data.pqrs_reclamos[0].municipio
      this.vb_lider = data.pqrs_reclamos[0].vb_lider
      this.vb_director = data.pqrs_reclamos[0].vb_director
      if ( parseInt(data.pqrs_reclamos[0].confidencialidad) === 0 ) {
        this.txtconfidencialidad = 'No'
      } else { this.txtconfidencialidad = 'Si' }
      switch (parseInt(this.id_estado)) {
        case 0:
          this.txtestado = 'Abierta'
          this.estado_color = '#5E35B1'
        break;
        case 1:
          this.txtestado = 'Asignado'
          this.estado_color = '#58af58'
        break;
        case 2:
          this.txtestado = 'En desarrollo'
          this.estado_color = '#FFC61A'
        break;
        case 4:
          this.txtestado = 'Cerrada'
          this.estado_color = '#F44336'
        break;
      }
    }
  },
  computed: {
    textBtnCommits () {
      return 'Ver historial'
    },
  },
  watch: {

  }
}
</script>
<style>
.theme--dark.v-text-field--solo > .v-input__control > .v-input__slot {
  background-color: transparent;
  margin-left: 5px;
}
#editor2 .ql-editor { min-height:600px }
</style>
