				tipoid:'',
				identificacion:'',
                apellido:'',
                nombre:'',
                codigocargo:'',
                codigoarea:'',	
                direccion:'',
                telefono:'',
                codigociudad:'',
                correo:'',
                codigoestado:'',
                codigotipocont:'',
                horasemanales:0,
				sexo:'',
				fechanacimiento:'',
				salariobasico:0,
				tiposalario: '',

