				data: [], 				
  				headers: [
  					{
			          text: 'Tipo Ident.',
			          align: 'left',
			          sortable: false,
			          value: 'nombretipoid',
                    },                    
                    { text: 'Identificacin', value: 'identificacion',sortable: false,align: 'left'},                    
					{ text: 'Nombre', value: 'nombrecompleto',sortable: false,align: 'left'},
					{ text: 'Sexo', value: 'sexo',sortable: false,align: 'left'},
                    { text: 'Cargo', value: 'nombrecargo',sortable: false,align: 'left'},
                    { text: 'Area', value: 'nombrearea',sortable: false,align: 'left'},                    
                    { text: 'Estado', value: 'nombreestado',sortable: false,align: 'left'},
                    { text: 'Tipo Contrato', value: 'descripciontipocont',sortable: false,align: 'left'},
                    { text: 'Horas Semanales (Invst.)', value: 'horasemanales',sortable: false,align: 'left'},
                    
                    { text: 'Accion', value: 'action',sortable: false}		        
  				],
  				
  				rules: {
					required: value => !!value || 'Campo requerido.',
					valor: value => value>0 || 'El Valor debe ser mayor a 0.',
					horassemana : value => value<=40 || 'Las Horas semanales no pueden superar las 40',
					email: value => {
		                const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
		                return pattern.test(value) || 'E-mail invlido.'
		            }
		          }				
	        }
	    },

	    filters: {
		},

	    methods: {
			
			showMessage (snack, time, color, text) {

			      this.$root.$emit('showSnack', {snack: snack, time: time, color: color, text: text})

		     },		     
			resetForm () {
                
                this.codigo = 0
				this.tipoid = '',
				this.identificacion = ''
                this.apellido = ''
                this.nombre = ''
                this.codigocargo = ''
                this.codigoarea = ''	
                this.direccion = ''
                this.telefono = ''
                this.codigociudad = ''
                this.correo = ''
                this.codigoestado = ''
                this.codigotipocont = ''
