            },

            traerDataPrincipal(){

		     	var _this = this
                this.loading = true

              	PersonalService.getData()
                  .then(function(res) {

                      if (res.request) {
                                        
                          if(res.data){

                            _this.data = res.data.data

                          }  else {
                            
                            _this.data = []
                          
                          }

                      } else {                        	

                      	_this.data = []                        	
                      }    
                      _this.loading = false                
                      
              	}).catch(function(err) {

              		_this.data = []   
              		_this.loading = false                
              		
				})

            },             
			
			traeEstados(){

		     	var _this = this
                this.loading = true
				
