                          }

                      } else {                        	

                      	_this.arrEstados = []                        	
                      }    
                      _this.loading = false                
                      
              	}).catch(function(err) {

              		_this.arrEstados = []   
              		_this.loading = false                
              		
				})

			},

			traeTiposId(){

		     	var _this = this
                				
