                          }  else {
                            
                            _this.arrTiposId = []
                          
                          }

                      } else {                        	

                      	_this.arrTiposId = []                        	
                      }    
                                  
                      
              	}).catch(function(err) {

              		_this.arrTiposId = []
              		
				})

			},

			traeCargos(){

		     	var _this = this
                				
