				})

			},

			traeAreas(){

		     	var _this = this
                				
              	areasService.getAreas()
                  .then(function(res) {

                      if (res.request) {
                                        
                          if(res.data){
							  	
							_this.arrAreas = res.data.data							

                          }  else {
                            
                            _this.arrAreas = []
                          
                          }

                      } else {                        	

                      	_this.arrAreas = []                        	
                      }    
                                  
                      
              	}).catch(function(err) {

              		_this.arrAreas = []
              		
