                      if (res.request) {
                                        
                          if(res.data){
							  	
							_this.arrCiudades = res.data.data							

                          }  else {
                            
                            _this.arrCiudades = []
                          
                          }

                      } else {                        	

                      	_this.arrCiudades = []                        	
                      }    
                                  
                      
              	}).catch(function(err) {

              		_this.arrCiudades = []
              		
				})

			},

			traeTiposContrato(){

		     	var _this = this
                				
              	tipocontratoService.getData()
                  .then(function(res) {

                      if (res.request) {
                                        
                          if(res.data){
							  	
							_this.arrTiposContrato = res.data.data							

                          }  else {
                            
                            _this.arrTiposContrato = []
                          
                          }

                      } else {                        	

                      	_this.arrTiposContrato = []                        	
                      }    
                                  
                      
              	}).catch(function(err) {

              		_this.arrTiposContrato = []
              		
				})

			},

			closeDialogoDel () {
				  
				this.dialog_del = false
			},

			deleteItem() {
				
				var _this = this
				this.loading = true

				PersonalService.DelData(_this.itemactual.codigo)
	              .then(function(res) {

	              	  if(res.statusText){

	              	  		_this.showMessage(true,3000,'success','Se elimin el Empleado con xito')

	              	  		_this.del_item = ''	              	  		
	              	  		_this.traerDataPrincipal()
							_this.closeDialogoDel()
								
							_this.agregaBitacora('ELIMINACION','SE ELIMIN EL EMPLEADO CON CODIGO:' + _this.itemactual.codigo + 
													', NOMBRE:' + _this.itemactual.nombre + ' ' + _this.itemactual.apellido)

	              	  }else{	                                         	

	                  		_this.showMessage(true,3000,'error',res.message) 	                  	
	                  } 	                   
						_this.del_item = ''						
	                    _this.loading = false	                 
	                   	
	          	}).catch(function(err) {

	          		_this.showMessage(true,3000,'error',err)	    
	          		_this.loading = false          	
	          		
				})

			},

			validaSave(){
				
				var codigotipoid = 0
				var codigocgo = 0
				var codigoare = 0
				var codigociu = 0
				var codigoest = 0
				var codigotipcont = 0

				if(typeof this.tipoid == 'object'){
					codigotipoid = this.tipoid.codigo

				} else {
					codigotipoid = this.tipoid					
				}

				if(typeof this.codigocargo == 'object'){
					codigocgo = this.codigocargo.codigo

				} else {
					codigocgo = this.codigocargo					
				}

				if(typeof this.codigoarea == 'object'){
					codigoare = this.codigoarea.codigo

				} else {
					codigoare = this.codigoarea					
				}

				if(typeof this.codigoestado == 'object'){
					codigoest = this.codigoestado.codigo

				} else {
					codigoest = this.codigoestado					
				}
				
				if(typeof this.codigoarea == 'object'){
					codigociu = this.codigociudad.codigo

				} else {
					codigociu = this.codigociudad					
				}
				
				if(typeof this.codigotipocont == 'object'){
					codigotipcont = this.codigotipocont.codigo

				} else {
					codigotipcont = this.codigotipocont					
				}				

                if(this.itemactual.tipoid == codigotipoid && this.itemactual.identificacion == this.identificacion && 
                    this.itemactual.apellido == this.apellido && this.itemactual.nombre == this.nombre &&
					this.itemactual.codigocargo == codigocgo && this.itemactual.codigoarea == codigoare && 
                    this.itemactual.direccion == this.direccion && this.itemactual.telefono == this.telefono && 
                    this.itemactual.codigociudad == codigociu && this.itemactual.correo == this.correo &&
                    this.itemactual.codigoestado == codigoest && this.itemactual.codigotipocont == codigotipcont &&
					this.itemactual.horasemanales == this.horasemanales && this.itemactual.sexo == this.sexo &&
					this.itemactual.fechanacimiento == this.fechanacimiento && 
					this.itemactual.salariobasico == this.limpiarMoneda(this.salariobasico) && 
					this.itemactual.tiposalario == this.tiposalario){

					return false

				} else {

					return true

				}	
			},
			saveitem(){

				var _this = this
                this.loading = true

				var datbit = ''
				
				var codigotipoid = 0
				var nombretipoid = ''
				var codigocgo = 0
				var nombrecgo = ''
				var codigoare = 0
				var nombreare = ''
				var codigociu = 0
				var nombreciu = ''
				var codigoest = 0
				var nombreest = ''
				var codigotipcont = 0
				var nombretipcont = ''

				if(typeof this.tipoid == 'object'){
					codigotipoid = this.tipoid.codigo
					nombretipoid = this.tipoid.descripcion

				} else {
					codigotipoid = this.tipoid
					nombretipoid = this.nombretipoid					
				}

				if(typeof this.codigocargo == 'object'){
					codigocgo = this.codigocargo.codigo
					nombrecgo = this.codigocargo.nombre

				} else {
					codigocgo = this.codigocargo	
					nombrecgo = this.nombrecargo				
				}

				if(typeof this.codigoarea == 'object'){
					codigoare = this.codigoarea.codigo
					nombreare = this.codigoarea.nombre
				} else {
					codigoare = this.codigoarea		
					nombreare = this.nombrearea			
				}

				if(typeof this.codigociudad == 'object'){
					codigociu = this.codigociudad.codigo
					nombreciu = this.codigociudad.nombrecompleto
				} else {
					codigociu = this.codigociudad	
					nombreciu = this.nombreciudad				
				}

				if(typeof this.codigoestado == 'object'){
					codigoest = this.codigoestado.codigo
					nombreest = this.codigoestado.descripcion
				} else {
					codigoest = this.codigoestado
					nombreest = this.nombreestado					
				}

				if(typeof this.codigotipocont == 'object'){
					codigotipcont = this.codigotipocont.codigo
					nombretipcont = this.codigotipocont.descripcion
				} else {
					codigotipcont = this.codigotipocont
					nombretipcont = this.descripciontipocont					
				}
				
                var data = {codigo:this.codigo,tipoid:codigotipoid,identificacion:this.identificacion,apellido:this.apellido,
							nombre:this.nombre,codigocargo:codigocgo,codigoarea:codigoare,
                            direccion:this.direccion,telefono:this.telefono,codigociudad:codigociu,
                            correo:this.correo,codigoestado:codigoest,codigotipocont:codigotipcont,
							horasemanales:this.horasemanales,sexo:this.sexo,
							fechanacimiento:this.fechanacimiento,salariobasico:this.limpiarMoneda(this.salariobasico),
							tiposalario:this.tiposalario}

				if(this.edit){

					var valida = this.validaSave()

					if(!valida){

						this.showMessage(true,3000,'error','No hay datos para actualizar')
						this.loading = false
						return;
					}

					datbit = ''

					if (this.itemactual.tipoid != codigotipoid)
                        datbit += 'Tipo Ident.: ' + this.itemactual.nombretipoid.toUpperCase() + ' a ' + nombretipoid.toUpperCase()
                        
                    if (this.itemactual.identificacion != this.identificacion)
						datbit += datbit == '' ? 'Identificacin: ' + this.itemactual.identificacion + ' a ' + this.identificacion : 
									', Identificacin: ' + this.itemactual.identificacion + ' a ' + this.identificacion   

					if (this.itemactual.apellido != this.apellido) {
						datbit += datbit == '' ? 'Apellidos: ' + this.itemactual.apellido + ' a ' + this.apellido : 
									', Apellidos: ' + this.itemactual.apellido + ' a ' + this.apellido 
                    }
                    
                    if (this.itemactual.nombre != this.nombre) {
						datbit += datbit == '' ? 'Nombres: ' + this.itemactual.nombre + ' a ' + this.nombre : 
									', Nombres: ' + this.itemactual.nombre + ' a ' + this.nombre 
					}

					if (this.itemactual.sexo != this.sexo) {
						datbit += datbit == '' ? 'Sexo: ' + this.itemactual.sexo + ' a ' + this.sexo : 
									', Sexo: ' + this.itemactual.sexo + ' a ' + this.sexo 
					}
                    
                    if (this.itemactual.codigocargo != codigocgo) {
						datbit += datbit == '' ? 'Cargo: ' + this.itemactual.nombrecargo + ' a ' + nombrecgo : 
									', Cargo: ' + this.itemactual.nombrecargo + ' a ' + nombrecgo 
                    }

					if (this.itemactual.codigoarea != codigoare) {
						datbit += datbit == '' ? 'Area: ' + this.itemactual.nombrearea + ' a ' + nombreare : 
									', Area: ' + this.itemactual.nombrearea + ' a ' + nombreare 
					}
					
                    if (this.itemactual.direccion != this.direccion) {
						datbit += datbit == '' ? 'Direccin: ' + this.itemactual.direccion + ' a ' + this.direccion : 
									', Direccin: ' + this.itemactual.direccion + ' a ' + this.direccion 
                    }

					if (this.itemactual.telefono != this.telefono) {
						datbit += datbit == '' ? 'Telfono: ' + this.itemactual.telefono + ' a ' + this.telefono : 
									', Telfono: ' + this.itemactual.telefono + ' a ' + this.telefono 
					}
					
					if (this.itemactual.codigociudad != codigociu) {
						datbit += datbit == '' ? 'Ciudad: ' + this.itemactual.nombreciudad + ' a ' + nombreciu : 
									', Ciudad: ' + this.itemactual.nombreciudad + ' a ' + nombreciu 
					}

					if (this.itemactual.correo != this.correo) {
						datbit += datbit == '' ? 'Correo: ' + this.itemactual.correo + ' a ' + this.correo : 
									', Correo: ' + this.itemactual.correo + ' a ' + this.correo 
					}

					if (this.itemactual.codigoestado != codigoest) {
						datbit += datbit == '' ? 'Estado: ' + this.itemactual.nombreestado + ' a ' + nombreest : 
									', Estado: ' + this.itemactual.nombreestado + ' a ' + nombreest 
					}

					if (this.itemactual.codigotipocont != codigotipcont) {
						datbit += datbit == '' ? 'Tipo de Contrato: ' + this.itemactual.descripciontipocont + ' a ' + nombretipcont : 
									', Tipo de Contrato: ' + this.itemactual.descripciontipocont + ' a ' + nombretipcont 
					}

					if (this.itemactual.horasemanales != this.horasemanales) {
						datbit += datbit == '' ? 'Horas Semanales: ' + this.itemactual.horasemanales + ' a ' + this.horasemanales : 
									', Horas Semanales: ' + this.itemactual.horasemanales + ' a ' + this.horasemanales 
					}
					
					if (this.itemactual.fechanacimiento != this.fechanacimiento) {
						datbit += datbit == '' ? 'Fecha Nacimiento: ' + this.itemactual.fechanacimiento + ' a ' + this.fechanacimiento : 
									', Fecha Nacimiento: ' + this.itemactual.fechanacimiento + ' a ' + this.fechanacimiento 
					}

					if (this.itemactual.salariobasico != this.limpiarMoneda(this.salariobasico)) {
						datbit += datbit == '' ? 'Salario Bsico: ' + this.itemactual.salariobasico + ' a ' + this.salariobasico : 
									', Salario Bsico: ' + this.itemactual.salariobasico + ' a ' + this.salariobasico 
					}
					
					if (this.itemactual.tiposalario != this.tiposalario) {

						let nomtiposalarioantes = this.itemactual.tiposalario == 'I' ? 'INTEGRAL' : 'ORDINARIO'
						let nomtiposalario = this.tiposalario == 'I' ? 'INTEGRAL' : 'ORDINARIO'

						datbit += datbit == '' ? 'Tipo Salario: ' + nomtiposalarioantes + ' a ' + nomtiposalario : 
									', Tipo Salario: ' + nomtiposalarioantes + ' a ' + nomtiposalario 
					}

					datbit = 'CODIGO: ' + this.codigo + ' (' + datbit + ')'

				} else {

                    datbit += 'NOMBRE: ' + this.nombre.toUpperCase() +  ' ' + this.apellido.toUpperCase() + ', Cargo: ' + nombrecgo + 
                                ', AREA: ' + nombreare  

				}				

                PersonalService.guardarData(data)
	              .then(function(res) {

	              	  if (res.response) {                           
                            
                          var error = res.response.data.message
                          _this.showMessage(true,3000,'error',error)

                      } else if(res.statusText){
	              	  		
	              	  		var mensaje = ''
							var accbitacora = ''

	              	  		if(_this.edit){
								mensaje = 'Actualiz'
								accbitacora = 'ACTUALIZACION'
							} else {
								mensaje = 'Agreg'
								accbitacora = 'CREACION'
							}

	              	  		_this.showMessage(true,3000,'success','Se ' + mensaje + ' el Empleado con xito')

	              	  		_this.del_item = ''
	              	  		_this.edit = -1	              	  		
	              	  		_this.traerDataPrincipal()
	              	  		_this.resetForm()
							_this.dialog = false								
							
							_this.agregaBitacora(accbitacora,datbit)

	              	  }else{	                                         	

	                  		_this.showMessage(true,3000,'error',res.message) 	                  	
	                  }

	                    _this.loading = false	                 
	                   	
	          	}).catch(function(err) {

	          		_this.showMessage(true,3000,'error',err)	    
	          		_this.loading = false          	
	          		
				})
			},			

			formatearValor(){

				let valoractual = this.salariobasico
				valoractual = this.limpiarMoneda(valoractual)

				valoractual = this.number_format(valoractual,2)

				this.salariobasico = valoractual
				
			},

			quitarFormato(){

				this.salariobasico = this.limpiarMoneda(this.salariobasico)

			},

			limpiarMoneda(val){				

				var nuevoval = val.toString().replace(',00','').replace(/[$.]/gi,'')
				nuevoval = nuevoval.replace(/[,]/gi,'.')
				
				return nuevoval				
			},

			number_format(amount, decimals) {

				amount += ''; // por si pasan un numero en vez de un string
				amount = parseFloat(amount.replace(/[^0-9\.]/g, '')) // elimino cualquier cosa que no sea numero o punto

				decimals = decimals || 0 // por si la variable no fue fue pasada

				// si no es un numero o es igual a cero retorno el mismo cero
				if (isNaN(amount) || amount === 0) 
					return parseFloat(0).toFixed(decimals)

				// si es mayor o menor que cero retorno el valor formateado como numero
				amount = '' + amount.toFixed(decimals)

				var amount_parts = amount.split('.'),
					regexp = /(\d+)(\d{3})/

				while (regexp.test(amount_parts[0]))
					amount_parts[0] = amount_parts[0].replace(regexp, '$1' + '.' + '$2')
				
				return amount_parts.join(',')
			},

			agregaBitacora(accion, detalle){              

              var data = {modulo:this.modulo,accion:accion,detalle:detalle}
              
              this.$root.$emit('agregaBitacora', data)

           }
		},
		
    	watch: {
		    edit: function (val) {

		       if(val)
		       {
		       		this.formTitle = 'Editar'

		       		this.codigo = this.itemactual.codigo
		       		this.tipoid = this.itemactual.tipoid
					this.identificacion = this.itemactual.identificacion
                    this.apellido = this.itemactual.apellido
                    this.nombre = this.itemactual.nombre
                    this.codigocargo = this.itemactual.codigocargo
                    this.codigoarea = this.itemactual.codigoarea
					this.direccion = this.itemactual.direccion
					this.telefono = this.itemactual.telefono
					this.codigociudad = this.itemactual.codigociudad
					this.correo = this.itemactual.correo
					this.codigoestado = this.itemactual.codigoestado
					this.codigotipocont = this.itemactual.codigotipocont
					this.horasemanales = this.itemactual.horasemanales
					this.sexo = this.itemactual.sexo

					this.fechanacimiento = this.itemactual.fechanacimiento == '0000-00-00' ? '' : this.itemactual.fechanacimiento
					this.salariobasico = this.number_format(this.limpiarMoneda(this.itemactual.salariobasico),2)

					this.tiposalario = this.itemactual.tiposalario

		       } else{

		       		this.resetForm()
		       	    this.formTitle = 'Nuevo'
		       }
		    }
		},
		mounted() {

			this.traerDataPrincipal()
			this.traeEstados()
			this.traeTiposId()
			this.traeCargos()
			this.traeAreas()
			this.traeCiudades()
			this.traeTiposContrato()
			this.agregaBitacora('INGRESO','INGRESO AL MODULO')
		},	
	}
	
</script>

