import Vue from 'vue'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import VueRouter from 'vue-router'

import Areas from './components/CAreas.vue'
import Cargos from './components/CCargos.vue'
import Personal from './components/CPersonal.vue'
import Perfiles from './components/CPerfiles.vue'
import Usuarios from './components/CUsuarios.vue'
import Bitacora from './components/CBitacora.vue'

import vpqr from './components/pqrs/vpqr.vue'
import detailpqr from './components/pqrs/detailpqr.vue'
import vdashboard from './components/pqrs/vdashboard.vue'
import vusuarios from './components/pqrs/vusuarios.vue'
import eventos from './components/pqrs/eventos.vue'
import vtipocomunicacion from './components/pqrs/vtipocomunicacion.vue'
import vejetematico from './components/pqrs/vejetematico.vue'
import vinstitucion from './components/pqrs/vinstitucion.vue'
import vperfil from './components/pqrs/vperfil.vue'
import vrespuestaautomatica from './components/pqrs/vrespuestaautomatica.vue'


Vue.use(VueRouter)

const routes = [
    {
      path: '/myapp',
      name: 'home',
      component: Home
    },
    {
      path: '/Areas',
      component: Areas,
      name: 'Areas',
    },
    {
      path: '/Cargos',
      component: Cargos,
      name: 'Cargos',
    },
    {
      path:'/Personal',
      component: Personal,
      name: 'Personal',
    },
    {
      path: '/Perfiles',
      component: Perfiles,
      name: 'Perfiles',
    },
    {
      path: '/Usuarios',
      component: Usuarios,
      name: 'Usuarios',
    },
    {
      path: '/Bitacora',
      component: Bitacora,
      name: 'Bitacora',
    },
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path:'/pqrs',
        component: vpqr,
        name: 'vpqr',
    },
    {
      path:'/pqrs/:id',
        component: detailpqr,
        name: 'detailpqr',
    },
    {
      path:'/pqrs_dashboard',
        component: vdashboard,
        name: 'vdashboard',
    },
    {
      path:'/pqrs_usuarios',
        component: vusuarios,
        name: 'vusuarios',
    },
    {
      path:'/pqrs_eventos',
        component: eventos,
        name: 'eventos',
    },
    {
      path:'/pqr_tipo_comunicacion',
        component: vtipocomunicacion,
        name: 'vtipocomunicacion',
    },
    {
      path:'/pqr_eje_tematico',
        component: vejetematico,
        name: 'vejetematico',
    },
    {
      path:'/pqr_instituciones',
        component: vinstitucion,
        name: 'vinstitucion',
    },
    {
      path:'/pqr_perfiles',
        component: vperfil,
        name: 'vperfil',
    },
    {
      path:'/pqr_respuestas',
        component: vrespuestaautomatica,
        name: 'vrespuestaautomatica',
    },
    {
      path: '*',
      redirect: '/myapp'
    }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

router.beforeEach((to, from, next) => {
  const publicPages = ['/', '/register'];
  const authRequired = !publicPages.includes(to.path);
  const self = Vue.prototype
  const loggedIn = self.$session.exists()
  if (authRequired && !loggedIn) {
    return next('/')
  }
  next()
})

export default router
