<template>
  <div>
    <v-card>
      <v-toolbar
        color="primary"
        dark
      >
        <v-btn icon dark @click="closeModal()">
          <v-icon>close</v-icon>
        </v-btn>
        <v-toolbar-title>Maestro Perfiles</v-toolbar-title>
      </v-toolbar>
      <v-card-subtitle>
        <v-row>
          <v-col cols="6">
            <v-autocomplete
              :items="areas"
              label="reas"
              item-value="arecod"
              item-text="arenom"
              v-model="txtareas"
              @change="loadUsers()"
            ></v-autocomplete>
          </v-col>
        </v-row>
        </v-card-subtitle>
        <v-card-text>
          <v-data-table
            class="my-4 elevation-2"
            :headers="headers"
            :items="items"
            :items-per-page="5"
            :loading="loading"
          >
            <template v-slot:item.responsable="{ item }">
              <v-layout class="align-center justify-center">
                <v-checkbox
                  color="primary"
                  v-model="item.responsable"
                  @change="validateResponsable(item)"
                ></v-checkbox>
              </v-layout>
            </template>
            <template v-slot:item.tipo="{ item }">
              <v-autocomplete
                :items="perfiles"
                item-value="value"
                item-text="text"
                v-model="item.tipo"
                @change="changePerfil(item)"
              ></v-autocomplete>
            </template>
          </v-data-table>
        </v-card-text>
    </v-card>
  </div>
</template>
<script>
import PqrService from "../../servicios/PqrService"
export default {
  data () {
    return {
      txtareas: '',
      dialog: false,
      loading: false,
      loading_delete: false,
      headers: [
        { text: 'Documento', align: 'center', value: 'documento', sortable: false },
        { text: 'Nombre', align: 'center', value: 'nombre', sortable: false },
        { text: 'rea', align: 'center', value: 'area', sortable: false },
        { text: 'Responsable', align: 'center', value: 'responsable', sortable: false },
        { text: 'Tipo', align: 'center', value: 'tipo', sortable: false }
      ],
      areas: [],
      items: [],
      perfiles: [
        { text: 'Por defecto', value: 0 },
        { text: 'Encargados', value: 1 },
        { text: 'Ventanilla', value: 2 },
        { text: 'Admin', value: 3 }
      ]
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    async changePerfil(item) {
      try {
        const {data} = await PqrService.updatePerfilPqrUser(item.iduser,item.tipo)
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
          this.loadUsers()
        } else {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      }
    },
    async validateResponsable(item) {
      try {
        const {data} = await PqrService.validateResponsable(item.documento,item.arecod)
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
          this.loadUsers()
        } else {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      }
    },
    async loadUsers() {
      try {
        this.loading = true
        const {data} = await PqrService.getPersonalArea(this.txtareas)
			  this.items = data
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    async getData() {
      try {
        this.loading = true
        const {data} = await PqrService.getDependencias()
			  this.areas = data.areas
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
  },
  computed: {

  }
}
</script>
