      medios: ['Comunicacin escrita - Carta','Comunicacin escrita - Correo electrnico','Comunicacin escrita - Memorando','Comunicacin escrita - Pgina web','Comunicacin escrita - Redes sociales','Comunicacin verbal - Llamada telefnica','No disponible'],
