<template>
  <div>
    <v-card>
      <v-toolbar
        color="primary"
        dark
      >
        <v-btn icon dark @click="closeModal()">
          <v-icon>close</v-icon>
        </v-btn>
        <v-toolbar-title>Maestro Instituciones</v-toolbar-title>
      </v-toolbar>
      <v-card-subtitle>
        <v-row>
          <v-col cols="6">
            Agregar registro
            <v-btn
              class="mx-2"
              fab
              dark
              small
              outlined
              color="primary"
              @click="openModal([])"
            >
              <v-icon dark>
                add
              </v-icon>
            </v-btn>
          </v-col>
          <v-col cols="6">
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Filtrar"
              single-line
              hide-details
            ></v-text-field>
          </v-col>
        </v-row>
        </v-card-subtitle>
        <v-card-text>
          <v-data-table
            class="my-4 elevation-2"
            :headers="headers"
            :items="items"
            :items-per-page="5"
            :loading="loading"
            :search="search"
          >
            <template v-slot:item.acciones="{ item }">
              <v-btn class="mx-1" small color="primary" icon @click="openModal(item)">
                <v-icon small>edit</v-icon>
              </v-btn>
              <v-btn :loading="loading_delete" class="mx-1" small color="primary" icon @click="deleteRecord(item)">
                <v-icon small>delete</v-icon>
              </v-btn>
            </template>
          </v-data-table>
        </v-card-text>
    </v-card>
    <v-dialog
      v-model="dialog"
      persistent
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">
         {{ formTitle }}
        </v-card-title>
        <v-card-text>
            <v-text-field
              class="mt-5"
              label="Nombre"
              outlined
              dense
              hide-details
              v-model="txtnombre"
              >
            </v-text-field> 
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="error darken-1"
            text
            @click="dialog = false, editItem = []"
          >
            Cerrar
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="save()"
            :loading="loading"
          >
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import PqrService from "../../servicios/PqrService"
export default {
  data () {
    return {
      search: '',
      txtnombre: '',
      dialog: false,
      loading: false,
      loading_delete: false,
      headers: [
        { text: 'Id', align: 'center', value: 'id', sortable: false },
        { text: 'Nombre', align: 'center', value: 'nombre', sortable: false },
        { text: 'Acciones', align: 'center', value: 'acciones', sortable: false }
      ],
      items: [],
      editItem: []
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    async deleteRecord(item) {
      try {
        this.loading_delete = true
        const {data} = await PqrService.deleteInstitucion(item.id)
        if (data.tipo === 'success') {
          this.$toasted.success(data.respuesta, { icon: 'done', duration: 3000 })
          this.dialog = false
          this.getData()
        } else {
          this.$toasted.error(data.respuesta, { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.loading_delete = false
      }
    },
    async save() {
      try {
        this.loading = true
        if (this.editItem.length === 0) {
          var data = await PqrService.saveInstitucion(this.txtnombre)
        } else {
          var data = await PqrService.updateInstitucion(this.editItem.id, this.txtnombre)
        }
        if (data.data.tipo === 'success') {
          this.$toasted.success(data.data.respuesta, { icon: 'done', duration: 3000 })
          this.dialog = false
          this.getData()
        } else {
          this.$toasted.error(data.data.respuesta, { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    openModal(item) {
      this.editItem = item
      this.txtnombre = this.editItem.nombre
      this.dialog = true
    },
    async getData() {
      try {
        this.loading = true
        const {data} = await PqrService.getInstituciones()
			  this.items = data.pqr_instituciones
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    closeModal () {
      this.$emit('closemodal')
    }
  },
  computed: {
    formTitle () {
      return this.editItem.length === 0 ? 'Agregar registro' : 'Editando registro'
    }
  }
}
</script>
