<template>
  <div>
    <v-card>
      <v-toolbar
        color="primary"
        dark
      >
        <v-btn icon dark @click="closeModal()">
          <v-icon>close</v-icon>
        </v-btn>
        <v-toolbar-title>Maestro tipos de comunicacin</v-toolbar-title>
      </v-toolbar>
        <v-card-title>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Filtrar"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-card-text>
          <v-data-table
            class="my-4 elevation-2"
            :headers="headers"
            :items="items"
            :items-per-page="5"
            :loading="loading"
            :search="search"
          >
            <template v-slot:item.acciones="{ item }">
              <v-btn class="mx-1" small color="primary" icon @click="openEdit(item)">
                <v-icon small>edit</v-icon>
              </v-btn>
            </template>
          </v-data-table>
        </v-card-text>
    </v-card>
    <v-dialog
      v-model="dialog"
      persistent
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">
          Editando {{ editItem.descripcion }}
        </v-card-title>
        <v-card-text>
            <v-text-field
              type="number"
              class="mt-5"
              label="Das respuesta"
              outlined
              dense
              hide-details
              prepend-icon="calendar_today"
              v-model="txtdias"
              >
            </v-text-field> 
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="error darken-1"
            text
            @click="dialog = false"
          >
            Cerrar
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="updateItem()"
            :loading="loading"
          >
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import PqrService from "../../servicios/PqrService"
export default {
  data () {
    return {
      search: '',
      txtdias: '',
      dialog: false,
      loading: false,
      headers: [
        { text: 'Descripcin', align: 'center', value: 'descripcion', sortable: false },
        { text: 'Das respuesta', align: 'center', value: 'dias_respuesta', sortable: false },
        { text: 'Acciones', align: 'center', value: 'acciones', sortable: false }
      ],
      items: [],
      editItem: []
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    async updateItem() {
      try {
        this.loading = true
        const {data} = await PqrService.updateTipoComunicacion(this.editItem.id, this.txtdias)
        if (data.respuesta === true) {
          this.$toasted.success('Registro actualizado correctamente.', { icon: 'done', duration: 3000 })
          this.dialog = false
          this.getData()
        } else {
          this.$toasted.error('Error al actualizar.', { icon: 'error', duration: 3000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    openEdit(item) {
      this.editItem = item
      this.txtdias = this.editItem.dias_respuesta
      this.dialog = true
    },
    async getData() {
      try {
        this.loading = true
        const {data} = await PqrService.getTipoComunicacion()
			  this.items = data.pqr_tipo_comunicacions
      } catch (error) {
        console.log(error)
      } finally {
        this.loading = false
      }
    },
    closeModal () {
      this.$emit('closemodal')
    }
  },
  computed: {
      
  }
}
</script>
