Pqrs.getData = function(filtro,valor,estado) {

  return new Promise(function(resolve, reject) {axios({
      method:'POST',
      url: configService.apiUrl + '/getPqrs',
      dataType: "json",
      data: {
        filtro: filtro,
        valor: valor,
        estado: estado
      }
      /*,
      headers:authHeader*/
    })
    .then(function(res) {

      resolve(res)

    })
    .catch(function(err) {

      resolve(err)

    })
  })

}

